<template>
  <section class="ad-ta">
    <!---->
      <div class="tools">
        <div class="input-wrapper">
          <input type="text" v-model="searchText">
        </div>
        <div class="btn-wrapper">
          <div class="search btn" @click="filter"></div>
          <div class="add btn" @click="add"></div>
        </div>
      </div>

    <!---->
    <el-table :data="adminArticles"  style="width: 100%" class="admin-list">
      <el-table-column prop="title" label="" width="300"></el-table-column>
      <el-table-column prop="tags" label="" width="150"> </el-table-column>
      <el-table-column prop="category" label="" width="150" sortable></el-table-column>
      <el-table-column prop="created" label="" width="150" sortable></el-table-column>
      <el-table-column prop="visited" label="" min-width="100" sortable></el-table-column>

      <el-table-column fixed="right" label="" width="150">
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible"> 
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm" >
        <el-form-item label=""><el-input v-model="editForm.title" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="editForm.tags" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="editForm.category" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="editForm.visited" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="editForm.created" auto-complete="off"></el-input></el-form-item>
        <el-form-item label="" > <el-input type="textarea" v-model="editForm.content" :rows="10"></el-input></el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" ></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="addFormVisible">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label=""><el-input v-model="addForm.title" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="addForm.tags" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="addForm.category" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="addForm.visited" auto-complete="off"></el-input></el-form-item>
        <el-form-item label=""><el-input v-model="addForm.created" auto-complete="off"></el-input></el-form-item>
        <el-form-item label="" > <el-input type="textarea" v-model="addForm.content" :rows="10"></el-input></el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import api from "@/api/index.js"
export default {
  data() {
    return {
      searchText: "",      
      adminArticles: [],
      editFormVisible: false, //
      editFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      //
      editForm: {
        title: '1',
        tags: "2",
        category: 3,
        created: 0,
        content: "",
      },
      addFormVisible: false, //
      addFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      //
      addForm: {
        title: '',
        tags: '',
        category: '',
        visited: 0,
        created: '',
        content: '',
      },
    };
  },
  methods: {
    // 
    filter(){
      api.search(this.searchText).then(res=>{
        let idPool = res.map(v=>v.id);

        this.adminArticles = this.adminArticles.filter(v=>{
          if(idPool.indexOf(v.id) !== -1) return true;
        })

      }).catch(err=>{
        alert("");
      })
    },
    
    // 
    add(){
      this.addFormVisible = true;
      this.addForm.created = new Date().toLocaleString().match(/\d+/g).slice(0,3).join('/');
    },
    // 
    addSubmit() {
      for(let key in this.addForm){
        let val = this.addForm[key];
        if(!this.warningEmpty(val)){
          alert("");
          return;
        }
      }
       api.addNewArticle(this.addForm).then(res=>{
        if(res.status === 200){
          this.getArticles();
          this.addFormVisible = false;
        }else{
          alert("ERROR!");
        }
      }).catch(err=>{
        alert("ERROR!");
      })
    },

    // 
    handleEdit(v) {
				this.editFormVisible = true;
        let {id,title,tags,category,created,visited} = v;
        api.getOneArticle(id).then(res=>{
        let content = res.data;
        this.editForm = {
          id,
          title,
          tags,
          category,
          created,
          visited,
          content,
        }
      }).catch(err=>{
        alert('')
      });
    },
    // 
    editSubmit() {
      api.updateEdit(this.editForm).then(res=>{
        if(res.status === 200){
          this.updateFromServer();
          this.editFormVisible = false;
        }else{
          alert("ERROR!");
        }
      }).catch(err=>{
        alert("ERROR!");
      })
    },

    // 
    handleDel(row) {
      let id = row.id;
      let flag = confirm(""+row.title);
      if(flag){
        api.delArticle(id).then(res=>{
          if(res.status === 200){
            this.getArticles();
          }else{
            alert("ERROR!");
          }
        }).catch(err=>{
          alert("ERROR!");
        })
      }
 
    },
  
    // 
    getAdminArticles(){
      this.adminArticles = [...this.articles].map((v) => {
        if(typeof v.tags === 'object'){
          v.tags = v.tags.join(",");
        }
        return v;
      });
    },

    // 
    updateFromServer(){
      this.getArticles();
      this.getAdminArticles();
    }



  },



  created() {
    this.updateFromServer();
  },
  watch: {
    articles() {
      this.getAdminArticles();
    },
    searchText(){
      if(this.searchText == ''){
          this.updateFromServer();
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.tools{
  width: 100%;
  height: 2.5rem;
  display: flex;
  .input-wrapper{
    margin-left: .6rem;
    flex: 14;
    input[type="text"]{
      width: 100%;
      height: 100%;
      border: 1px solid black;
    }
  }
  .btn-wrapper{
    flex: 2;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-right: .6rem;
    .btn{
      width: 4rem;
      background-color: black;
      color: white;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      font-weight: 800;
      font-size: 1rem;
      &:hover{
        background-color: #f10215;
        border: 1px solid white;
      }
    }
  }
}


.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.ad-ta {
  width: 100%;
  .el-tool-wrapper {
    width: 100%;
    .toolbar {
      width: 100%;
      .input-type {
        width: 90%;
        display: inline-block;
        box-sizing: border-box;
        margin: 0;
        .el-input {
          width: 70rem;
        }
      }
      .query {
        display: inline-block;
        box-sizing: border-box;
        position: absolute;
        margin: 0 .5rem;
        right: 4.5rem;
        width: 3rem;
        transform: translate(-55%, 0);
        .el-button {
          width: 100%;
          height: 100%;
        }
      }
      .add {
        position: absolute;
        right: 0;
        width: 3rem;
        transform: translate(-55%, 0);
        display: inline-block;
        box-sizing: border-box;
        margin: 0;
        .el-button {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}

    .el-button {
      background-color: black;
      color: white;
      &:hover {
        color: white;
        background-color: #f10215;
      }
    }

</style>